import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import "./HomePage.css";
import crvena_kap from '../crvena_kap.png';
import tablica from '../tabela_KG.png';


class HomePageComponent extends Component {

    constructor(props) {
        super(props)
        this.state={
            showTablica:true,
            showDarovanje:false,
            showKontakti:false,
            showPitanja:false,
            showMoguce:false
        }
        this.changeContent = this.changeContent.bind(this)
    }



    changeContent (e,tekst) {
        this.state.showTablica=false;
        this.state.showDarovanje=false;
        this.state.showKontakti=false;
        this.state.showPitanja=false;
        this.state.showMoguce=false;
        if (tekst===1) this.setState({showTablica:true})
        if (tekst===2) this.setState({showDarovanje:true})
        if (tekst===3) this.setState({showKontakti:true})
        if (tekst===4) this.setState({showMoguce:true})
        if (tekst===5) this.setState({showPitanja:true})

    }


    render() {
        return (
            <div className="frame">
                <div className="header">
                    <div className="headerFrame">
                        <img className="kap_img" src={crvena_kap} alt="Crvena kap"/>
                    </div>
                    <div className="headerFrame">
                        <p>Federalna bolnica +387 33 123 456</p>
                        <h1>Doniranje krvi u Bosni i Hercegovini</h1>
                    </div>
                    
                </div>
                <div className="frameRow">
                <div className="lijevi">
                    <button className="ListaButtona" onClick={(e)=> {this.changeContent(e,1)}}>Tablica krvnih grupa</button>
                    
                    <button className="ListaButtona" onClick={(e)=> {this.changeContent(e,2)}}>Zato darovati krv?</button>
                    
                    <button className="ListaButtona" onClick={(e)=> {this.changeContent(e,3)}}>Kontakt informacije, gdje i kada?</button>
                    
                    <button className="ListaButtona" onClick={(e)=> {this.changeContent(e,4)}}>Koliko esto je mogue darovati krv?</button>
                    
                    <button className="ListaButtona" onClick={(e)=> {this.changeContent(e,5)}}>esta pitanja o darivanju krvi</button>
                </div>
                <div className="sredina">
                    <label className="naslov"><u>Spasimo ivote zajedno, donirajte krv.</u></label>
                    <br/>
                    <br/>
                    
                    {this.state.showTablica? 
                    <div>

                         <p className="paragraf">U tabeli su oznaene pozitivne kompatibilnosti krvnih grupa primaoca i donatora. <br/>
                        Dvije specifine krvne grupe su AB+ i 0-. Osoba ija je krvna grupa AB+ je univerzalni primalac.<br/>
                         A osoba ija je krvna grupa 0- je univerzalni davalac.</p>
                        <img className="img" src={tablica}/>
                    </div>
                    :null}
                    
                    {this.state.showDarovanje? 
                    <div>
                        <label className="naslov"> Zato darovati krv?</label>
                        <p className="paragraf"> 
                        Krv je nezamjeniv lijek povrijeenima, oboljelim od hematolokih i onkolokih oboljenja, <br/>
                        oboljelim kojima je potrebna hirurka intervencija itd.<br/><br/>
                        Krv se ne moe prozvesti, jedini nain pomoi osobama kojim je potrebna krv je darovanjem iste. <br/>
                        Darovanje krvi predstavlja sponu izmeu zdravog i bolesnog dijela populacije.<br/> 
                        Da bi se obezbijedio ovaj lijek za one kojim je najpotrebniji potrebno je animirati to <br/>
                         vei broj populacije koji ele pomoi drugima.</p>
                    </div>
                    :null}

                    {this.state.showKontakti? 
                    <div>
                         <label className="naslov">Kontakt informacije:</label>
                        <br/>
                        <br/>
                        <label>Adresa</label>
                        <p className="paragraf"> 
                        Zavod za transfuzijsku medicinu Federacije Bosne i Hercegovine <br/>
                            ekalua 86, 71000 Sarajevo<br/>
                            Bosna i Hercegovina<br/>Web: www.ztmfbih.ba<br/>e-mail: ztm@ztmfbih.ba</p>
                        <label>Odsjek za promociju davalatva:</label>
                            <p className="paragraf">Tel: +387 33 567 304 <br/>
                            Tel: +387 33 567 302<br/>
                            e-mail: animacija@ztmfbih.ba </p>
                        <label className="naslov">Kada i gdje je mogue darovati krv?</label>
                        <br/>
                        <br/>
                        <p className="paragraf"> 
                        Darovati krv moete od ponedjeljka do petka u Zavodu za transfuzijsku medicinu FBiH.<br/>
                        Zavod za transfuzijsku medicinu se nalazi u ulici ekalua 86 u neposrednoj blizini <br/>Medicinskog fakulteta i Srednje Zubotehnike kole</p>
                    </div>
                    :null}

                    {this.state.showMoguce? 
                    <div>
                        <label className="naslov">Koliko esto je mogue darovati krv?</label>
                        <br/>
                        <br/>
                        <label>Mukarci</label>
                        <p className="paragraf"> 
                        Mukarci mogu darovati krv 4 puta godinje, odnosno svaka 3 mjeseca.</p>
                        <label>ene</label>
                        <p className="paragraf">
                        ene mogu darovati krv 3 puta godinje, odnosno svaka 4 mjeseca.</p>
                    </div>
                    :null}
                    {this.state.showPitanja? 
                    <div>
                         <label className="naslov">esta pitanja o darivanju krvi</label>
                        <br/>
                        <br/>
                        <label className="naslov">TETI LI DARIVANJE KRVI ZDRAVLJU?</label>
                        <p className="paragraf"> 
                        Darivanje krvi ne teti zdravlju ako se provedu svi propisani postupci pri odabiru darivaoca krvi.<br/> 
                        Svaka zdrava osoba izmeu 18 i 65 godina starosti moe bez opasnosti za svoje zdravlje darovati krv, <br/>
                        3 do 4 puta u toku jedne godine.<br/>
                        Zdrav organizam darivaoca krvi vrlo brzo u potpunosti nadoknauje koliinu i sve sastavne dijelove<br/>
                         darovane krvi: ve unutar 24 sata organizam nadoknadi tekui dio krvi - plazmu i njene sastojke, <br/>
                         broj trombocita i leukocita. Eritrociti se nadoknade unutar 4 do 6 sedmica.<br/>
                         Darivanje krvi najvie utjee na eljezo koje se u obliku hemoglobina nalazi u eritrocitima <br/>
                         (crvene krvne stanice). Darivanjem 450 mL krvi darivalac gubi oko 200 mg eljeza. <br/>
                         Organizam nadoknauje gubitak eljeza u roku 1 do 2 mjeseca poveanom apsorpcijom iz hrane.<br/>
                         Prije svakog darivanja krvi obavezno provjeravamo zdravstveno stanje darivaoca krvi i<br/>
                          vrijednost hemoglobinau njegovoj krvi.</p>

                        <label className="naslov">MOE LI SE DARIVANJEM KRVI ZARAZITI OD NEKE BOLESTI?</label>
                        <p className="paragraf">
                        Mogunost zaraze u toku darivanja krvi ne postoji.<br/>
                        Sav pribor za uzimanje krvi - igle, plastine kese i ostali materijal koji se koristi <br/>
                         prilikom uzimanja krvi su sterilni i za jednokratnu su upotrebu. Pribor je napravljen <br/>
                         na nain koji onemoguuje njegovu ponovnu upotrebu.</p>

                         <label className="naslov">RAZVIJA LI SE OVISNOST ZA DARIVANJE KRVI?</label>
                        <p className="paragraf">
                        Darivanje krvi ne uzrokuje ovisnost!<br/>
                        Darivanje krvi moe se zapoeti i prestati u svako doba izmeu 18. i 65. godine ivota.<br/>
                        Darivanjem krvi ne nastaju nikakve tetne tjelesne promjene ili posljedice po organizam.<br/>
                        Neki ljudi se ipak bolje osjeaju nakon to daruju krv i zato daruju krv nekoliko puta godinje.<br/>
                        Ta je pojava ea u osoba s blago povienim krvnim tlakom. Kod tih osoba je darivanje krvi ujedno<br/>
                        i nain ublaavanja simptoma uzrokovanih blagim povienjem krvnog tlaka, ali nije nain lijeenja<br/>
                         povienog tlaka.</p>

                    </div>
                    :null}

                </div>
                <div className="desni">
                <br/>

                <label  className="naslov" >Za vie detalja o darivanju krvi posjetite <a href="https://www.ztmfbih.ba/">ZZTMFBIH</a>.</label>
                        <br/>
                        <br/>
                    <label className="naslov">Prijavite se na sistem za donaciju krvi.</label>
                    
                    <button className="Login"> <Link className="openLogin" to="/login">Login</Link></button>
                    <br></br>
                    
                </div>
                </div>
            </div>
        )
    }
}
export default HomePageComponent